@use 'base/variables' as v; 
@use 'base/mixins' as mx; 


.nuestro-menu{
    background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, v.$primario, v.$primario);
    background-repeat: no-repeat;
    background-position: center 5rem, center 92%, center 100%;
    background-size: 200%;
    padding: 15rem 5rem 20rem 5rem;

    @include mx.tablet { 
        padding: 15rem 0 20rem 0;
        background-size: 100%; 
     }
     @include mx.desktop { 
         background-size: 50%, 50%, 100%;
      }

}

.grid-menu{
    @include mx.tablet { 
        @include mx.grid(2, 2rem); 
     }
}

.menu{
    h3{
        color: v.$blanco;
    }
    li{

        display: flex;
        justify-content: space-between;
        align-items: center;

        p{


            color: v.$blanco;
            font-size: 2.2rem;
            line-height: 1.5;
            margin: 0 0 2.5rem 0;

            span{
                display: block;
                color: v.$secundario;
            }
        }
        .precio{
            font-size: 4rem;
            font-weight: 700;

        }
    }
}