@use '../base/variables' as v;
@use '../base/mixins' as mx;

.contenido-menu {
    background-image: url(../img/cafe.jpg);
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 100%;
    aspect-ratio: 3 / 6;
    @include mx.mobile { 
        aspect-ratio: 3 / 5;
    }
    @include mx.tablet { 
        @include mx.grid(2, v.$separacion);
        aspect-ratio: 16 / 6;
        background-size: 40%;
        background-position: bottom right;
    }

    .texto {
        p {
            font-size: 2rem;
        }
   
    }
}
